
.slide-enter-active {
  -webkit-animation: slide-in 800ms ease-out forwards;
          animation: slide-in 800ms ease-out forwards;
}
.slide-leave-active {
  -webkit-animation: slide-out 400ms ease-out forwards;
          animation: slide-out 400ms ease-out forwards;
}
@-webkit-keyframes slide-in {
0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes slide-in {
0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@-webkit-keyframes slide-out {
0% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    opacity: 0;
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}
}
@keyframes slide-out {
0% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    opacity: 0;
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}
}


nav {
  width: 100%;
  background: #3fba1a;
}
ul {
  width: 80%;
  padding: .7rem .2rem;
}
ul li {
  list-style: none;
  display: inline-block;
}
.container img {
    width: 100%;
}
.logo-link {
    width: 5%;
    margin-left: 1rem;
    margin-top: 1rem;
}
.logo-text {
    font-size: 1.5em;
    font-style: italic;
}
.container ul a {
    color: #fff;
    -webkit-transform: scale(.9);
            transform: scale(.9);
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    border: 1px; 
    border-radius: 50px;
}
.container ul a:hover {
    background-color: orangered;
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}
.toggle {
  cursor: pointer;
  width: 100%;
  padding: .7rem 2rem;
  background: #227708;
  text-align: right;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  color: #fff;
  font-size: 1.6rem;
  display: none;
}
@media(max-width: 768px) {
.toggle {
    display: block;
}
ul {
    width: 100%;
    display: none;
}
ul li {
    display: block;
    text-align: center;
}
.active {
    display: block;
}
}
@media(max-width: 481px) {
.logo-link {
    display: block;
    margin: 0 auto;
    width: 10%;
}
.logo-text {
    text-align: center;
    display: block;
}
}

.footer {
    text-align: center;
}
footer {
    color: #fff;
}
footer a {
    color: #fff;
}
footer a:hover {
    color: red;
}
.container-margin {
    margin-bottom: 2rem;
}

.card .body {
text-align: center;
}
.card .title {
    margin-bottom: 1em;
    font-size: 4em;
}
.card .subtitle {
    margin-bottom: 1em;
}
.input-search {
    width: 50%;
    height: 3em;
    border: 1px solid  #33cc33;
    border-radius: 2em;
    padding-left: 1em;
}
.input-search:focus {
    outline: none;
}
.input-search::-webkit-input-placeholder {
    color:  #adebad;
    font-style: italic;
}
.input-search::-moz-placeholder {
    color:  #adebad;
    font-style: italic;
}
.input-search::-ms-input-placeholder {
    color:  #adebad;
    font-style: italic;
}
.input-search::placeholder {
    color:  #adebad;
    font-style: italic;
}
.btn-search {
    width: 30%;
    margin-top: 1em;
    border-radius: 1em;
}
.warning-message {
    margin-top: 2%;
}
@media(max-width: 481px) {
.btn-search {
        width:40%;
}
.input-search {
        width: 90%;
}
.card .title {
        font-size: 2.5em;
}
}

.card .wrapper-current {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 1px;
    grid-auto-rows: minmax(30px, auto);
    color:#fff;
    background: #d6f5d6;
}
.bg-black {
    background: #000;
}
.weather-icon {
    display: inline-block;
    vertical-align: 200%;
    margin-bottom:0;
}
.temp-number {
    font-size: 4em;
    vertical-align: 30%;
    color: #ff9900;
}
.temp-degree {
    vertical-align: 150%;
    font-size: 1.5em;
}
.nested {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.margin-tb {
    margin-bottom: 0;
    margin-top: 5%;
}
.margin-t {
    margin-top: 10%;
}
.feels-like {
    margin-top: 9%;
    margin-bottom: 0;
}
.back-btn {
    width: 30%;
    margin: 0 auto;
    cursor: pointer;
}
.forecast-btn {
    width: 30%;
    margin: 0 auto;
    cursor: pointer;
}
@media(max-width: 768px) {
.back-btn {
        width: 60%;
        display: inline-block;
}
}
@media(max-width: 481px) {
.card .wrapper-current {
        display: grid;
        grid-template-columns: 1fr;
}
.back-btn {
        width: 60%;
        display: inline-block;
}
}


.wrapper-forecast-title {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-row-gap: 1px;
    grid-auto-rows: minmax(60px, auto);
    color:#fff;
    background: #000;
    place-items: center;
}
@media(max-width: 1024px) {
.hide-l {
        display: none;
}
.wrapper-forecast-title {
        grid-template-columns: repeat(7, 1fr);
}
}
@media(max-width: 768px) {
.hide-m {
        display: none;
}
.wrapper-forecast-title {
        grid-template-columns: repeat(5, 1fr);
}
}
@media(max-width: 481px) {
.hide-s {
        display: none;
}
.wrapper-forecast-title {
        grid-template-columns: repeat(3, 1fr);
}
.card-title--small {
        font-size: 1rem;
}
}



.wrapper-forecast {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-row-gap: 20px;
    color: #fff;
    place-items:center;
    background: #55552b;
}
@media(max-width: 1024px) {
.hide-l {
        display: none;
}
.wrapper-forecast {
        grid-template-columns: repeat(7, 1fr);
}
}
@media(max-width: 768px) {
.hide-m {
        display: none;
}
.wrapper-forecast {
        grid-template-columns: repeat(5, 1fr);
}
}
@media(max-width: 481px) {
.hide-s {
        display: none;
}
.wrapper-forecast {
        grid-template-columns: repeat(3, 1fr);
}
}

.about {
    padding-top: 20px;
}
.about-par {
    width: 90%;
    margin: 20px auto;
}
@media(max-width: 481px) {
.about-title {
        font-size: 1.1rem;
}
}
